@import './Styles.scss';

.premiumModal {
  position: fixed;
  top: 0;
  z-index: 3;
  height: 100vh;
  width: 100vw;
  background-color: $blue;
  display: flex;
  align-items: center;
  justify-content: center;
  &__background {
    position: absolute;
    z-index: 3;
  }
  &__exitIcon {
    position: absolute;
    z-index: 3;
    right: 50px;
    top: 50px;
    background-color: white;
    border-radius: 50px;
    transition: all 0.3s;
    &:hover {
      cursor: pointer;
      transform: rotate(90deg);
    }
  }
  &__container {
    z-index: 4;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 25px;
    background-color: white;
    border-radius: 15px;
    padding: 50px;
    text-align: center;
    h1 {
      grid-column: 1 / span 2;
      font-weight: 500;
      font-size: 36px;
    }
    h2 {
      grid-column: 1 / span 2;
      font-size: 24px;
      margin-top: 15px;
      color: $grey;
    }
  }
  &__button {
    margin-top: 50px;
    border: 1px solid $blue-grey;
    border-radius: 15px;
    overflow: hidden;
    &Top {
      font-weight: 600;
      border-bottom: 1px solid $blue-grey;
      letter-spacing: 1.2px;
      text-transform: uppercase;
      padding: 25px 0;
    }
    &Bottom {
      display: flex;
      flex-direction: column;
      padding: 25px;
      h3 {
        margin-top: 25px;
        font-size: 60px;
      }
      h4 {
        margin-top: 10px;
        font-size: 18px;
      }
      a {
        border: 1px solid $blue-grey;
        font-size: 24px;
        font-weight: 600;
        margin-top: 40px;
        border-radius: 5px;
        color: $blue;
        width: 100%;
        background-color: white;
        width: 250px;
        padding: 20px;
        &:hover {
          border-color: $blue;
        }
      }
    }
  }
  .premiumModal__button.-blue {
    color: white;
    .premiumModal__buttonTop {
      background-color: #0068cc;
    }
    .premiumModal__buttonBottom {
      background-color: $blue;
    }
  }
}
