@import './Styles.scss';

.header {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  img {
    max-height: 40px;
  }
  &__menu {
    position: relative;
  }
  &__menuButton {
    display: flex;
    flex-direction: row;
    align-items: center;
    img:first-child {
      margin-right: 10px;
    }
  }
  &__dropdown {
    position: absolute;
    z-index: 1;
    width: 175px;
    top: 40px;
    right: 0;
    border: 1px solid $blue-grey;
    background-color: white;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    overflow: hidden;
  }
  &__dropdownItem {
    box-sizing: border-box;
    height: 50px;
    padding: 15px;
    font-size: 16px;
    font-weight: 500;
    text-align: left;
    line-height: 1em;
    border-bottom: 1px solid $blue-grey;
    &:last-child {
      border-bottom: noen;
    }
    &:hover {
      background-color: $light-grey;
    }
  }
}
