@import './Styles.scss';

.day__expired {
  display: flex;
  flex-direction: column;
  h2 {
    font-size: 24px;
    line-height: 30px;
    font-weight: bold;
    text-align: center;
  }
  h3 {
    margin-top: 5px;
    font-size: 18px;
    line-height: 24px;
    text-align: center;
    margin-bottom: 20px;
  }
  &Item {
    background-color: $light-grey;
    border: 2px solid $light-grey;
    padding: 15px;
    margin-bottom: 10px;
    border-radius: 5px;
    font-weight: 500;
    display: flex;
    flex-direction: row;
    p {
      margin-left: 10px;
    }
    &:hover {
      cursor: pointer;
    }
  }
  &Item[data-selected='true'] {
    border: 2px solid $blue;
    background-color: $blue-lighter;
  }
  &Item[data-error='true'] {
    border: 2px solid $red;
  }
  button {
    font-weight: bold;
    font-size: 18px;
    padding: 15px;
    border-radius: 5px;
    margin-top: 10px;
  }
  &SubscribeButton {
    background-color: $blue;
    color: white;
  }
  &ContinueButton {
    border: 1px solid $blue-grey;
    color: $blue;
  }
}
