@import './Styles.scss';

.login {
  background-color: $blue;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  &__logo {
    max-height: 40px;
    margin-bottom: 25px;
  }
  &__container {
    display: flex;
    flex-direction: column;
    border-radius: 15px;
    background-color: white;
    padding: 50px;
    max-width: 500px;
    @media (max-width: 375px) {
      border-radius: 0;
      padding: 25px;
    }
  }
  &__error {
    margin: 0 0 15px 0;
    font-size: 18px;
    color: $red;
    text-align: center;
  }
  &__separator {
    position: relative;
    margin-top: 15px;
    hr {
      width: 100%;
      border-bottom: 1px solid $light-grey;
      margin-bottom: 25px;
    }
    small {
      position: absolute;
      text-align: center;
      width: 50px;
      background-color: white;
      color: $grey;
      left: calc(50% - 25px);
    }
  }
  input {
    width: 100%;
    outline: none;
    border: none;
    background-color: $light-grey;
    border: 1px solid transparent;
    border-radius: 5px;
    padding: 20px;
    margin-bottom: 15px;
    font-size: 18px;
    color: $dark-grey;
    font-weight: 500;
    background-repeat: no-repeat;
    background-position: 360px 25px;
    &:focus {
      background-color: white;
      border: 1px solid $blue;
      background-image: none;
    }
    ::placeholder {
      font-weight: 400;
      color: #4d5560;
    }
    &[aria-invalid='true'] {
      border: 1px solid $red;
      background-image: url('../assets/images/inputError.svg');
    }
  }
  button {
    height: 60px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    margin-bottom: 10px;
    color: white;
    font-size: 20px;
    font-weight: 600;
    border-radius: 5px;
    img {
      position: absolute;
      top: 10px;
      left: 10px;
      height: 40px;
    }
    &:last-child {
      margin-bottom: 0;
    }
    &[data-type='Email'] {
      background-color: $blue;
    }
    &[data-type='Google'] {
      background-color: #4285f4;
    }
    &[data-type='Facebook'] {
      background-color: #2d5c98;
    }
    &[data-type='Text'] {
      font-size: 18px;
      height: 30px;
      color: $dark-grey;
      span {
        margin-left: 10px;
        color: #ffa031;
        border-color: #ffa031;
      }
    }
  }
}
